@keyframes moveUp {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes moveUpAndRotate {
    0% {
        transform: translateX(100%) rotate(180deg);
    }
    100% {
        transform: translateX(0%) rotate(180deg);
    }
}

.home__pathway {
    @include media('<small') {
        .pathway {
            .pathway__cards {
                .card--pathway {
                    &:not(:nth-of-type(1)) {
                        margin-top: $measure--small1;
                        flex-direction: row;
                        flex-wrap: nowrap;
                    }
                }
            }
        }
    }

    @include media('>=1080p') {
        .triangle__block {
            display: none;
        }
    }

    @include media('<small') {
        .pathway {
            @include default-margin();

            .card--pathway__anchor {
                box-shadow: 0 0 $measure--small2 rgba(0, 0, 0, 0.15);
            }
        }
    }

    @include media('>=small') {
        .pathway {
            @include standard-box();

            .card--pathway__anchor {
                box-shadow: 0 0 $measure--basic rgba(0, 0, 0, 0.15);
            }
        }
    }

    // Generic
    margin: 0 0 $measure--medium1 0;

    // VI
    &.in-view {
        .triangle__block--left {
            animation: moveUp $transition-duration--longer1;
        }

        .triangle__block--right {
            animation: moveUpAndRotate $transition-duration--longer1;
        }
    }

    // Card block
    .pathway {
        .pathway__cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            max-width: unset;
            grid-column-gap: $measure--small2;
            margin: $measure--basic 0 $measure--small4 0;
            position: relative;
            z-index: z-index('above');
            @include media('<small') {
                display: block;
            }
        }

        .card--pathway {
            list-style-type: none;
            opacity: 0;
            transition: opacity $transition-duration--longer3;
            margin-top: 0;
            z-index: z-index('above');
            background: $color-purewhite;

            &.in-view {
                opacity: 1;
            }

            .card--pathway__anchor {
                display: block;
                background-image: none;
                border: 0;
                height: 100%;
                transition: transform $transition-duration--long
                        cubic-bezier(0.4, 0, 0.2, 1),
                    box-shadow $transition-duration--long
                        cubic-bezier(0.4, 0, 0.2, 1);

                &:focus,
                &:active {
                    outline-style: $border-style-solid;
                    background: transparent;
                }

                &:hover {
                    @include media('hover') {
                        .card--pathway__img {
                            transform: scale(1.1);
                        }
                        transform: translateY(-2px);
                        box-shadow: 0 0 $measure--basic rgba(0, 0, 0, 0.25);
                    }
                }

                &.card--pathway__border {
                    border-top: $measure--half $border-style-solid
                        $color-brand-primary;
                }

                .wrap--card--pathway__heading {
                    position: relative;
                    padding-left: $measure--basic;
                    flex-basis: 100%;
                }
            }
        }

        .wrap--icon {
            display: flex;
            margin-bottom: $measure--threequarters;

            .wrap--icon__img {
                height: $measure--medium1;
            }
        }

        .wrap--card--pathway__img {
            overflow: hidden;
            line-height: 0;

            .card--pathway__img {
                transition: transform $transition-duration--long
                    cubic-bezier(0.4, 0, 0.2, 1);
            }

            & + .card--pathway__txt {
                padding-top: $measure--basic;
            }
        }

        .card--pathway__txt {
            padding: $measure--small2;
            color: initial;
        }

        .card--pathway__heading {
            font-weight: $font-weight-medium;
            display: inline;
            text-decoration: underline;
        }

        .card--pathway__desc {
            font-weight: $font-weight-normal;
        }

        @include media('<mobile') {
            .card--pathway {
                .card--pathway__anchor {
                    .wrap--icon {
                        flex-wrap: wrap;
                    }

                    .wrap--icon__img {
                        margin-right: $measure--quarter;
                    }

                    .line {
                        border-top: $border-width-thicker $border-style-solid
                            $color-brand-primary;
                        flex: 1;
                        margin: auto;
                    }

                    .wrap--card--pathway__heading {
                        padding: $measure--half 0 0 0;
                        flex-basis: 100%;
                    }
                }
            }
        }

        @include media('>=mobile', '<small') {
            .card--pathway__content {
                display: flex;
            }

            .wrap--icon {
                display: block;
            }

            .card--pathway {
                .card--pathway__anchor {
                    .wrap--card--pathway__heading {
                        padding-left: 0;
                    }
                }

                .wrap--card--pathway__img {
                    margin: $measure--basic;
                    flex-basis: 40%;

                    & + .card--pathway__txt {
                        padding: $measure--basic $measure--small2
                            $measure--small2 $measure--half;
                        flex-basis: 60%;
                    }
                }

                .wrap--icon__img {
                    margin-bottom: $measure--quarter;
                }
            }
        }
    }
}
