@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes scaleIn {
    0% {
        transform: scale(1.15);
    }
    100% {
        transform: scale(1.1);
    }
}

.home__social {
    grid-column: 1/-1;
    position: relative;

    .row {
        display: flex;
    }

    .wrap--social__img {
        overflow: hidden;

        &:nth-of-type(1) {
            .social__img {
                &.in-view {
                    animation: fadeIn 1.8s 0s, scaleIn 1.8s 0s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(2) {
            .social__img {
                &.in-view {
                    animation: fadeIn 1.8s 0.1s, scaleIn 1.8s 0.1s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(3) {
            .social__img {
                &.in-view {
                    animation: fadeIn 1.8s 0.2s, scaleIn 1.8s 0.2s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(4) {
            .social__img {
                &.in-view {
                    animation: fadeIn 1.8s 0.3s, scaleIn 1.8s 0.3s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(5) {
            .social__img {
                &.in-view {
                    animation: fadeIn 1.8s 0.4s, scaleIn 1.8s 0.4s;
                    animation-fill-mode: forwards;
                }
            }
        }
    }

    .social__img {
        opacity: 0;
        filter: brightness(0.65);
    }

    .card {
        position: absolute;
        background: $color-purewhite;
        padding: $measure--medium6 $measure--medium2;
        transition: top 0.3s;
        z-index: z-index('above');
    }

    .card__content__body {
        border-bottom: $border-width-hairline $border-style-solid $color-gray6;
        padding-bottom: $measure--basic;
        margin-bottom: $measure--basic;
    }

    .card__content__heading {
        margin-top: 0;
    }

    .card__post-strap {
        .icon__wrapper {
            background-color: $color-brand-primary;
            border-radius: $measure--basic;
            color: $color-purewhite;
            display: inline-flex;
            height: $measure--small4;
            width: $measure--small4;
            margin-right: $measure--half;

            .icon {
                margin: auto;
                &.fa-instagram {
                    &:before {
                        @include gammatype();
                    }
                }
            }

            .line {
                margin-bottom: $measure--small1;
            }
        }

        a.anchor-discreet {
            color: $color-brand-primary;
            border-bottom-color: $color-brand-primary;
            font-weight: $font-weight-medium;
            bottom: $measure--quarter / 2;

            &:hover {
                @include media('hover') {
                    &::before {
                        opacity: 0;
                        transition: none;
                    }
                }
            }

            &:focus {
                outline-style: solid;
            }
        }
    }

    @include media('<mobile') {
        .card {
            padding-left: $measure--basic;
            padding-right: $measure--basic;
        }
        .wrap--social__img {
            &:nth-of-type(3) {
                display: none;
            }
        }
    }

    @include media('<tablet') {
        .card {
            padding-left: $measure--small4;
            padding-right: $measure--small4;
        }
        .wrap--social__img {
            &:nth-of-type(4),
            &:nth-of-type(5) {
                display: none;
            }
        }
    }

    @include media('<small') {
        .card {
            padding-left: $measure--medium1;
            padding-right: $measure--medium1;
            @include default-margin();
            top: 50%;
            transform: translateY(-50%);
        }
    }

    @include media('>=small', '<1080p') {
        .wrap--card {
            @include standard-box();
            max-width: $width--max;
        }
    }

    @include media('>=small') {
        .wrap--card {
            @include standard-box();
            max-width: $width--max;
        }

        .card {
            &.in-view {
                top: 10rem;
            }
            &:not(.in-view) {
                opacity: 0;
                top: 17rem;
            }
        }
    }

    @include media('>=1080p') {
        .wrap--card {
            padding-left: $measure--medium7;
            padding-right: $measure--medium7;
            margin: 0;
            width: 30vw;
        }

        // Limit image block width, similar to other content elements on page
        @include standard-box();
        width: $width--max;
    }
}
