.home-banner {
    margin: 0;
    position: relative;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: $color-brand-primary;
    background-size: cover;

    .hero-image img,
    video {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        object-fit: cover;

        @include media('<small') {
            min-height: 450px;
        }

        @include media('>=small') {
            min-height: 600px;
        }

        @include media('>=large') {
            max-height: 100vh;
        }
    }
    
    .hero-image__veil {
        position: relative;
        min-height: 100%;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: z-index('above');
            background-color: rgba(0,0,0,.55);
            width: 100%;
            height: 100%;
        }
    }

    .cta-block {
        a {
            display: inline-block;
            @include cta--basic(
                $color-purewhite,
                $color-red50,
                $color-purewhite,
                $color-red80,
                $color-nearblack,
                round
            );

            &:focus,
            &:active {
                @include anchor-default-outline($color-purewhite);
            }
        }
    }
}

.home-banner--video {
    .hero-image {
        position: absolute;
        top: 0;
    }
}

.js {
    .home-banner--video {
        video::-webkit-media-controls-overlay-play-button {
            display: none;
            -webkit-appearance: none;
        }
    
        video::-webkit-media-controls-panel {
            display: none !important;
            -webkit-appearance: none;
        }
    
        video::-webkit-media-controls-play-button {
            display: none !important;
            -webkit-appearance: none;
        }
    
        video::-webkit-media-controls-start-playback-button {
            display: none !important;
            -webkit-appearance: none;
        }
    
        video::-webkit-media-controls {
            display:none !important;
            -webkit-appearance: none;
        }
    }
}

.home-banner__content {
    @include standard-grid();
    width: 100%;
    height: 100%;
    z-index: z-index('inpage');
    position: absolute;
    top: 0;
}

.home-banner__content__text {
    position: absolute;
    top: 50%;
    grid-column: 2 / -2;
    color: $color-purewhite;
    width: 100%;
    z-index: z-index('inpage');
    @include default-padding();

    p {
        text-shadow: 0px 0px 5px rgba(0,0,0,0.65);
    }

    p, ul {
        text-align: left;
    }

    @include media('>=tablet') {
        top: 60%;
        grid-column: 2 / -3;
        p, ul {
            text-align: center;
            margin: auto;
        }
    }
}

.home-banner__strapline {
    grid-column: 1 / -1;
    @include default-padding();

    @include media('>=small') {
        grid-column: 2 / -1;
        padding: 0;
    }
}

.home-banner__strapline__text {
    padding: $measure--medium3 0;
    background-color: transparent; 
    height: $measure--huge5; // 10rem
    width: $measure--huge5;

    @include media('>=tablet') {
        background-color: $color-brand-primary;
        padding: $measure--medium3 $measure--basic; // 3rem 1rem
    }
}

.banner__video__button {
    border-radius: 50%;
    color: $color-purewhite;
    height: $measure--medium3; // 3rem
    width: $measure--medium3;
    background-color: $color-gray10;
    position: absolute;
    top: 80%;
    right: $measure--small4; // 2rem
    z-index: z-index('inpage'); // 2
    padding: $measure--half; // 0.5rem
    transition: all .2s ease-in-out;

    @include media('>=tablet') {
        top: 60%;
    }

    &:hover {
        @include media('hover'){
            background-color: $color-brand-primary;
            transform: scale(1.2);
        }
    }

    &:focus {
        background-color: $color-brand-primary;
    }

    &:focus,
    &:active {
        outline: $border-width-default $border-style-solid $color-purewhite;
        outline-offset: 3px;
    }
    
    .button__img--pause {
        display: block;
    }
    .button__img--play {
        display: none;
    }

    &.play {
        background-color: $color-gray10;
        &:hover {
            @include media('hover'){
                background-color: $color-brand-primary;
            }
        }

        .button__img--pause {
            display: none;
        }
        .button__img--play {
            display: block;
        }
    
    }
}
 