.home__promo {
    position: relative;
    background-image: url('https://www.city.ac.uk/__data/assets/image/0007/720673/student-life-desktop.jpg');
    background-size: cover;

    &[data-position='right'] {
        .wrap--home__promo__card {
            margin-left: auto;
        }

        .triangle__block--right {
            display: none;
        }
    }

    &[data-position='left'] {
        .triangle__block--left {
            display: none;
        }
    }

    .home__promo__text p {
        @include heading-font-features();
        @include font-family('heading');
    }

    .wrap--home__promo__card {
        padding: $measure--large2 0;
        box-sizing: border-box;
        transition: transform 0.3s, opacity 0.3s;
        z-index: z-index('above');
        width: 50%;
    }

    .home__promo__title {
        margin-top: 0;
    }

    .home__promo__img {
        width: 100%;
    }

    .home__promo__card {
        position: relative;
        z-index: z-index('above');
        background-color: $color-purewhite;
        padding: $measure--medium1;
    }

    .basic-cta--bright {
        &:focus,
        &:active {
            outline-style: $border-style-solid;
        }
    }

    .triangle__block {
        filter: brightness(0) invert(1);
        bottom: 0;
        position: absolute;

        &.triangle__block--left {
            margin-bottom: 0;
            transform: rotate(270deg);
            left: -1px;
        }

        &.triangle__block--right {
            right: 0;
            transform: rotate(180deg);
        }
    }

    // Below viewports
    @include media('<small') {
        .home__promo__container {
            @include default-margin();
        }
    }

    @include media('<tablet') {
        .wrap--home__promo__card {
            width: 75%;
        }
    }

    @include media('<mobile') {
        background-image: url('https://www.city.ac.uk/__data/assets/image/0008/720674/student-life-mobile.jpg');
        .wrap--home__promo__card {
            width: 100%;
        }
    }

    // Above viewports
    @include media('>=small') {
        .home__promo__container {
            @include standard-box();
        }

        .wrap--home__promo__card {
            &.in-view {
                top: 50%;
                opacity: 1;
                transform: translateY(0%);
            }
            &:not(.in-view) {
                opacity: 0;
                transform: translateY(100%);
            }
        }
    }
}
